<template>
  <div class="functionlist tableImage">
    <v-row
      class="pa-2"
      appear
      tag="transition-group"
      @before-enter="beforeEnter"
      @enter="enter"
    >
      <v-col
        class="align-center pa-0 py-1"
        cols="6"
        md="3"
        v-for="(item, index) in items"
        :key="item.label"
        v-ripple
        :data-index="index"
      >
        <v-alert
          outlined
          :color="item.color"
          border="left"
          class="white ma-0 py-2"
          v-if="item.keyName != 'exit'"
        >
          <router-link :to="item.route" tag="div">
            <h2 class>{{ item.label }}</h2>
            <v-img :src="item.image" aspect-ratio="2.5" contain></v-img>
          </router-link>
        </v-alert>

        <v-alert
          outlined
          :color="item.color"
          border="left"
          class="white ma-0 py-2"
          v-if="item.keyName == 'exit'"
        >
          <router-link :to="item.route" tag="div" @click="logout">
            <h2 class>{{ item.label }}</h2>
            <v-img :src="item.image" aspect-ratio="2.5" contain></v-img>
          </router-link>
        </v-alert>
      </v-col>
    </v-row>

    <v-dialog v-model="mainboardDialog" persistent scrollable>
      <v-card>
        <v-toolbar dark color="warning">
          <v-btn icon dark @click.stop="mainboardDialog = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title class="font-weight-black">公告欄</v-toolbar-title>
        </v-toolbar>
        <Mainboard viewType="inFunction" />
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import gsap from "gsap";
import Mainboard from "@/views/main/MainboardList";

export default {
  components: { Mainboard },

  data: () => ({
    mainboardDialog: "",
  }),

  computed: {
    items() {
      return this.$store.state.authKeys.filter(
        (item) =>
          this.$store.state.pData[item.authKey] == "true" ||
          item.authKey == "pass" ||
          item.authKey == "exit_key"
      );
    },
  },

  mounted() {
    //2021.8.26判斷是否為藥師權限 -> 只能顯示病歷管理畫面
    if (this.$store.state.pData.pharmacist_key){
      this.$router.push("/main/userlist");
    }

    this.mainboardDialog = this.$store.state.cData.showMainboard;
  },

  methods: {
    logout() {
      this.$store.commit("logined", false);
      this.$router.push("/login");
      sessionStorage.clear(); //清除sessionStorage
    },
    // checkAuth(authkey) {
    //   if (this.$store.state.pData) {
    //     if (
    //       this.$store.state.pData[authkey] == "true" ||
    //       authkey == "pass" ||
    //       authkey == "exit_key"
    //     ) {
    //       return true;
    //     }
    //   }
    // },
    //gsap 動畫
    beforeEnter(el) {
      // console.log('beforeEnter')
      el.style.opacity = 0;
      el.style.transform = "translateX(100px)";
    },
    enter(el, done) {
      // console.log('enter')
      gsap.to(el, {
        opacity: 1,
        x: 0,
        duration: 0.8,
        onComplete: done,
        delay: el.dataset.index * 0.1,
      });
    },
  },
};
</script>